import React, { Component } from "react";
import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";
import tatto from "../../assets/img/rt.jpg";
import salon_img from "../../assets/img/salon.png";
import yoga_img from "../../assets/img/yoga.png";
import spa_img from "../../assets/img/1.png";
import Header from "../../layouts/header";
import Footer from "../../layouts/footer";
import "./list.css";
import request from "superagent";
// import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import managedWorkingDay from "../../common";
import AuthHelperMethod from '../../../components/AuthHelperMethods';
const Auth = new AuthHelperMethod();
class List extends Component {
  constructor(props) {
    super(props);

    this.state = {
      rating: 4.5,
      categoryDropdown: false,
      lists: [],
      services: [],
      stableList: [],
      quickViewDisplay: [
        { id: null, display: "display-none", displaySide: "" }
      ],
      isLoading: true
    };
    // console.log(this.state);
  }

  quickViewIn = (id, index) => {
    // alert(index);
    request
      .get(`http://localhost:9000/getVendorData/services/${id}`)
      .then(res => {
        let parseData = JSON.parse(res.text);
        if (parseData) {
          // console.log(parseData);
          this.setState({ services: parseData });
        }
      })
      .catch(err => console.log(err.stack));
    let quickViewDisplay = { ...this.state.quickViewDisplay };
    quickViewDisplay.id = id;
    quickViewDisplay.display = "display-block";
    quickViewDisplay.displaySide =
      index % 3 === 0 ? "popup-card-right" : "popup-card-left";
    this.setState({ quickViewDisplay });
  };
  quickViewOut = () => {
    let quickViewDisplay = { ...this.state.quickViewDisplay };
    quickViewDisplay.id = null;
    quickViewDisplay.display = "display-none";
    this.setState({ quickViewDisplay });
  };
  test = () => {
    console.log("Test===========================================");
    alert("test");
  };

  filterByGender = gender => {
    const lists = this.state.stableList.filter(c => c.vendor_type === gender);
    this.setState({ lists });
    // alert(gender);
  };
  compareValues = (key, order = "asc") => {
    return function(a, b) {
      if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
        return 0;
      }
      const varA = typeof a[key] === "string" ? a[key].toUpperCase() : a[key];
      const varB = typeof b[key] === "string" ? b[key].toUpperCase() : b[key];

      let comparison = 0;
      if (varA > varB) {
        comparison = 1;
      } else if (varA < varB) {
        comparison = -1;
      }
      return order == "desc" ? comparison * -1 : comparison;
    };
  };
  sortByName = () => {
    console.log(
      this.state.lists.sort(this.compareValues("business_name", "asc"))
    );
  };
  convertTime = time => {
    // alert(time);
    let conTime;
    if (time >= 12) {
      if (time === 12) {
        conTime = "12:00 pm";
      } else if (time === 24) {
        conTime = "12:00 am";
      } else {
        conTime = time - 12 + ":00 pm";
      }
    } else {
      conTime = time + ":00 am";
    }
    return conTime;
  };
  componentWillMount() {
    request
      .get("http://localhost:9000/getVendorData/")
      .then(res => {
        // console.log(res);
        // this.setState({ lists: res.text });
        let parseData = JSON.parse(res.text);
        // console.log(parseData);

        this.setState({
          lists: parseData,
          stableList: parseData,
          isLoading: false
        });
      })
      .catch(err => console.log(err.stack));
  }
  handleLogout = () =>{
    Auth.logout();
    this.props.history.replace("/list");
    // alert("Logout");
  }
  render() {
    const { lists, services, isLoading } = this.state;
    return (
      <React.Fragment>
        {/* {this.test()} */}
        <Header isLoading={isLoading} handleLogout={this.handleLogout}/>
        <div className="container-fluid border py-3 pt-5 px-5 owl-theme-cantainer">
          <OwlCarousel
            className="owl-carousel owl-theme-img owl-theme"
            loop
            margin={10}
            items={4}
          >
            <div className="item">
              <img src={tatto} className="px-3" alt="list-slider 1" />
            </div>
            <div className="item">
              <img src={salon_img} className="px-3" alt="list-slider 2" />
            </div>
            <div className="item">
              <img src={yoga_img} className="px-3" alt="list-slider 3" />
            </div>
            <div className="item">
              <img src={tatto} className="px-3" alt="list-slider 4" />
            </div>
            <div className="item">
              <img src={spa_img} className="px-3" alt="list-slider 5" />
            </div>
            <div className="item">
              <img src={salon_img} className="px-3" alt="list-slider 6" />
            </div>
            <div className="item">
              <img src={yoga_img} className="px-3" alt="list-slider 7" />
            </div>
          </OwlCarousel>
        </div>

        <main
          role="main"
          className="container-fluid wowtis-main-container pt-0"
        >
          <div className="container">
            <div className="row">
              {/* Filter Block *this filter block show only desktop view*/}
              <div className="d-none d-md-block col-md-3 wow slideInLeft">
                {/* <!-- End of Sort by user rating filter scetion -->
                        <!-- Sort by Gender filter scetion --> */}
                <article className="card-group-item border border-top-0">
                  <header className="card-header p-0">
                    <h6 className="title m-0 py-2">
                      <span className="ml-4"> Sort By Gender</span>
                    </h6>
                  </header>
                  <div className="filter-content">
                    <div className="card-body">
                      <form>
                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="sortbygender"
                            value="Male"
                            onChange={() => this.filterByGender("Male")}
                          />
                          <span className="form-check-label">Male</span>
                        </label>

                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="sortbygender"
                            value="Female"
                            onChange={() => this.filterByGender("Female")}
                          />
                          <span className="form-check-label">Female</span>
                        </label>
                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="sortbygender"
                            value="Unisex"
                            onChange={() => this.filterByGender("Unisex")}
                          />
                          <span className="form-check-label">Unisex</span>
                        </label>
                      </form>
                    </div>
                  </div>
                </article>
                {/* 
                         * End of Sort by Gender filter scetion
                        */}
                {/* Sort by price filter scetion */}
                <article className="card-group-item border border-top-0">
                  <header className="card-header p-0">
                    <h6 className="title m-0 py-2">
                      <span className="ml-4 "> Sort By Discount</span>
                    </h6>
                  </header>
                  <div className="filter-content">
                    <div className="card-body">
                      <form>
                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="sortbyprice"
                            value=""
                          />
                          <span className="form-check-label">Low to High</span>
                        </label>
                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="sortbyprice"
                            value=""
                          />
                          <span className="form-check-label">High to Low</span>
                        </label>
                      </form>
                    </div>
                    {/* card-body.// */}
                  </div>
                </article>
                {/* End of Sort by price filter scetion -->
                        <!-- Sort by price filter User Rating */}
                <article className="card-group-item border border-top-0">
                  <header className="card-header p-0">
                    <h6 className="title m-0 py-2">
                      <span className="ml-3"> Sort By User Rating</span>
                    </h6>
                  </header>
                  <div className="filter-content">
                    <div className="card-body">
                      <form>
                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="userrating"
                            value=""
                          />
                          <span className="form-check-label">High to Low</span>
                        </label>

                        <label className="form-check">
                          <input
                            className="form-check-input"
                            type="radio"
                            name="userrating"
                            value=""
                          />
                          <span className="form-check-label">Low to High</span>
                        </label>
                      </form>
                    </div>
                  </div>
                </article>
              </div>
              {/* /* End Filter Block */}

              {/*  Listing our authorized vendors */}
              {/* {this.getService("777")} */}
              <section className="col-md-9 col-sm-12">
                <div className="row  px-2 list-card">
                  {/* <!-- single block of lists --> */}
                  {lists.length > 0 ? (
                    lists.map((list, index) => (
                      <div
                        key={list.vendor_id}
                        onMouseLeave={() => this.quickViewOut()}
                        className="card-deck m-0 p-0 mb-5 col-sm-6 col-lg-4   mx-auto wow pulse"
                        data-wow-offset="10"
                      >
                        {/* <img src={test} alt="" srcset="" /> */}
                        <Link to={`/view/?id=${list.vendor_id}`}>
                          <div className="card shadow">
                            {/* <!-- Show Background image And Quick view Button --> */}
                            <div
                              className="card-img-top "
                              style={{
                                background: `url(${spa_img}) no-repeat center center`,
                                backgroundSize: "cover"
                              }}
                            >
                              <button
                                onMouseEnter={() =>
                                  this.quickViewIn(list.vendor_id, index)}
                                className="d-none d-sm-block btn-sm quick-view-btn px-5 btn btn-outline-light"
                              >
                                Quick View
                              </button>
                            </div>
                            {/* <!-- End Background image --> */}

                            {/* <!-- Information Section of vendor --> */}
                            <div className="card-body">
                              <h5 className="card-title mb-0 text-bold ">
                                {list.business_name}
                              </h5>
                              <small className="light-color ">
                                {managedWorkingDay(list.working_day)}
                                <br />
                                <strong className="text-success">
                                  Open :
                                </strong>
                                {this.convertTime(parseInt(list.opening_time))}
                                {/* {list.locality},{list.city} */}
                                {/* {list.address} */}&mdash;
                                {this.convertTime(parseInt(list.closing_time))}
                              </small>

                              <div className="pt-3 text text-center ">
                                <button
                                  className={
                                    (this.state.rating > 3.5
                                      ? "btn-success "
                                      : "btn-warning ") +
                                    "btn  btn-sm py-0 float-left "
                                  }
                                >
                                  <i className="fas fa-star" />
                                  {this.state.rating}
                                </button>
                                {/* <span>{list.time} Mins</span> */}
                                <span className="float-right ">
                                  {/*  {list.amount} */}
                                </span>
                              </div>
                            </div>
                            {/* <!-- End Information Section of vendor --> */}
                          </div>

                          {/* <!-- Popup section of quick view --> */}
                          <div
                            data-wow-offset="10"
                            data-wow-duration="5s"
                            className={
                              "popup-card pt-5 pl-3 flipInX animated " +
                              (this.state.quickViewDisplay.display ===
                                "display-block" &&
                              this.state.quickViewDisplay.id === list.vendor_id
                                ? this.state.quickViewDisplay.displaySide
                                : "display-none ")
                            }
                          >
                            <div className="row p-0 m-0">
                              <div className="col-sm-12 col-md-5 popup-card-details-con p-0 m-0 ">
                                <h5 className="card-title mb-0 text-bold ">
                                  {list.business_name}
                                </h5>
                                <small className="light-color ">
                                  {list.full_address}
                                </small>
                              </div>
                              <div className="col-md-7 popup-card-img-con d-sm-none d-md-block pl-0 ml-0">
                                <div className="row">
                                  {services.length > 0 ? (
                                    services.map(service => (
                                      <div key={service._id} className="col-sm-6 pb-1">
                                        <img
                                          src={spa_img}
                                          className="img-fluid w-100"
                                        />
                                        <div
                                          style={{
                                            lineHeight: 0.8
                                          }}
                                        >
                                          <small>{service.service_name}</small>
                                        </div>
                                      </div>
                                    ))
                                  ) : (
                                    <h3>No record found....</h3>
                                  )}
                                </div>
                              </div>
                            </div>
                          </div>
                          {/* <!-- End of Popup section of quick view --> */}
                        </Link>
                      </div>
                    ))
                  ) : (
                    <div className="text-center col">
                      <h3>No Record Found............</h3>
                    </div>
                  )}
                </div>
              </section>

              {/* <!-- End Listing our authorized vendors --> */}
            </div>
          </div>
        </main>
        <Footer />
      </React.Fragment>
    );
  }
}

export default List;
