import React, { Component } from 'react'
import "../../assets/css/animate.css";

class Profile extends Component{
    constructor(props){
        super(props);
        this.state = {
         
            
        }
       
    }
    handleEdit = () =>{
        this.props.handleUserEdit();
    }
    handleSubmit = e =>{
        e.preventDefault();
        this.props.handleUpdateUser();
    }
    onChangeName = (value)=>{
        this.props.onChangeName(value);
    }
    onChangeEmail = (value)=>{
        this.props.onChangeEmail(value);
    }
    onChangePhone = (value)=>{
        this.props.onChangePhone(value);
    }
    onChangePassword = (value)=>{
        this.props.onChangePassword(value);
    }
    
    render(){
      
        const {user,onChangeName, onChangeEmail, onChangePhone, onChangePassword,edit} = this.props;
        return(
            <div>
                <h3>Manage Profile {this.state.email}</h3>
                <div className="row mt-5 ">
                    <form onSubmit={this.handleSubmit} className="form w-100">
                    <table className="table bg-white">
                        <thead>
                            <tr>
                            <th>Name</th>
                            <th>Email</th>
                            <th>Phone</th>
                            <th></th>
                            <th></th>
                            </tr>
                        </thead>
                        <tbody>
                           <tr>
                                <td>{user.name} </td>
                                <td> {user.email} </td>
                                <td> {user.phone} </td>
                                <td></td>
                                <td><button type="button"  className={edit ? "d-none" : "btn btn-link"} onClick={this.handleEdit}>edit</button></td>

                           </tr>
                           <tr className={edit ? "slideInDown animated" : "d-none"}>
                                <td><input type="text" placeholder="Enter name" value={user.name} onChange={e=>this.onChangeName(e.target.value)} className="form-control"/> </td>
                                <td> <input type="email" placeholder="Enter email" value={user.email} onChange={e=>this.onChangeEmail(e.target.value)} className="form-control"/> </td>
                                <td> <input type="text" placeholder="phone" value={user.phone} onChange={e=>this.onChangePhone(e.target.value)} className="form-control number" minLength="10" maxLength="10"/> </td>
                                <td><input type="password" className="form-control"  onChange={e=>this.onChangePassword(e.target.value)} placeholder="Enter password"/></td>
                                <td><button className="btn btn-outline-success" type="submit">Save</button></td>
                           </tr>
                          
                        </tbody>
                    </table>
                    </form>
                </div>
            </div>
        );
    }
}

export default Profile;
