import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import Index from "./front/pages/index/index";
import List from "./front/pages/list/list";
// import ParentList from "./front/pages/list/parentList";
import View from "./front/pages/view/view.jsx";
import OWL from "./owl";
import Test from "./test";
import UserDashboard from "./user/pages/dashboard/UserDashboard";
import { BrowserRouter as Router, Route } from "react-router-dom";
import { createBrowserHistory } from "history";

import * as serviceWorker from "./serviceWorker";
const history = createBrowserHistory();

ReactDOM.render(
  <React.Fragment>
    <Router history={history}>
      <Route path="/" exact component={Index} />
      <Route path="/list" exact component={List} />
      <Route path="/view" exact component={View} />
      <Route path="/view/:id" exact component={View} />
      <Route path="/owl" exact component={OWL} />
      <Route path="/test" exact component={Test} />
      <Route path="/user" exact component={UserDashboard} />
    </Router>;
  </React.Fragment>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
