import React, { Component } from 'react'

class Booking extends Component{
    constructor(props){
        super(props);

    }

    render(){
        const {bookings,getUserBookingsByOrderNo,activeBooking} = this.props;
        return(
            <React.Fragment>
                  {/* <!--========Booking Tab --> */}
                
                    {/* <!--========Booked Service --> */}
                    <table className="table bg-white">
                        <thead>
                            <tr>
                                <th>Order no</th>
                                <th>Booking date</th>
                                <th>Booking time</th>
                                <th>Total amount</th>
                                <th>action</th>
                            </tr>
                        </thead>
                        <tbody>
                            {bookings.map(b=>(
                                <tr key={b._id}>
                                   <td>{ b.order_no }</td>
                                   <td>{ b.booking_date }</td>
                                   <td>{ b.booking_time }</td>
                                   <td>{ b.booking_total_amount }</td>
                                   <td><button className="btn btn-link" onClick={()=>getUserBookingsByOrderNo(b.order_no)}>view</button></td>
                              </tr>
                            ))}
                        </tbody>
                    </table>
                 
                    {/* <!--========End Booked Service --> */}
                 
                  {/* <!--========End Booking Tab --> */}
            </React.Fragment>
        );
    }
} 

export default Booking;