import React, { Component } from "react";

// import "jquery";
import "bootstrap/dist/js/bootstrap.bundle.min";
import "bootstrap/dist/js/bootstrap.min";
import "../assets/css/floating.css";
import back_img from "../assets/img/back-sign-2.png";
// import WOW from "wowjs";
import { Link } from "react-router-dom";
import AuthHelperMethods from "../../components/AuthHelperMethods";

// import List from "../pages/list/list";
// import { createBrowserHistory } from "history";
// const history = createBrowserHistory();

class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      signupName: "",
      signupEmail: "",
      signupPassword: "",
      signupPhone: "",
      redirect: true,
      loginPhone: "",
      loginFormErr: ""
    };
  }
  Auth = new AuthHelperMethods();
  componentDidMount() {
    // new WOW.WOW({
    //   live: true
    // }).init();
    // console.log(window.pageYOffset);
  }
  handleLogin = e => {
    e.preventDefault();
    this.props.handleLogin(this.state.loginPhone);
   
  };
  handleSignup = e => {
    e.preventDefault();
    const { signupEmail, signupName, signupPassword, signupPhone } = this.state;
    let data = {
      name: signupName,
      email: signupEmail,
      phone: signupPhone,
      password: signupPassword
    };
    this.props.handleSignup(data);
  };

  render() {
    const { login, signup, toggleLoginOpen, toggleSignupOpen,signupFormError,cancelLoginSignup } = this.props;
    const {
      signupName,
      signupEmail,
      signupPhone,
      signupPassword,
      loginPhone,
      loginFormErr
    } = this.state;
    const re = /^[0-9\b]+$/;
    return (
      <main>
        <div id="overlay" />
        <footer
          className="jumbotron footer mt-auto py-3 pb-0 mb-0 container-fluid "
          style={{ minHeight: 300 }}
        >
          <div className="container">
            <div className="row text-center  pt-3">
              <div className="col-md mb-3">
                <Link className="wowtis-header-logo " to="/">
                  <strong>
                    <span>W</span>
                    <span>owtis</span>
                  </strong>
                </Link>
                <p className="text-muted text-justify">
                  With a sphere of influence across several countries across the
                  globe including the United States, Canada, and the UK, along
                  with a decade of spearheading innovation in the field of cloud
                  hosting, Sagenext Infotech has cemented its position as a
                  leading tax and accounting software hosting provider. S
                </p>
              </div>
              <div className="col-md pt-3  mb-3">
                <h3>Useful - links</h3>
                <hr className="mb-0" />
                <Link to="/">Contact Us</Link>
                <hr className="mb-0" />
                <Link to="/">Sitemap</Link>
                <hr className="mb-0" />
                <Link to="/">terms of use</Link>
                <hr className="mb-0" />
                <Link to="/">Privacy and policy</Link>
                <hr className="mb-0" />
                <Link to="/">List your business</Link>
              </div>

              <div className="col-md pt-3 text-left mb-3">
                <h4>Experiance Wowtis APP on Mobile</h4>
                <div>
                  <img
                    src="https://d6yq2luad9xli.cloudfront.net/images/web/playstore.png"
                    className="rounded"
                    alt="play store"
                    height="40"
                  />
                  <p className="pt-3">
                    <strong>Reach Us</strong>
                  </p>
                  <p className="text-muted">
                    Call 8097121888 to speak to us today!
                  </p>
                  <p>
                    <strong>KEEP IN TOUCH</strong>
                  </p>
                  <p className="f-180">
                    <Link to="/">
                      <i className="fab fa-facebook-square" />
                    </Link>
                    <Link to="/">
                      <i className="fab fa-linkedin" />
                    </Link>
                    <Link to="/">
                      <i className="fab fa-twitter-square" />
                    </Link>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </footer>
        <footer>
          <div
            className="py-2"
            style={{ background: "#333333", color: "#808080" }}
          >
            <div className="col-md text-center">
              Copyright  2019 <Link to="/">Sagenext Infotech.</Link> All Rights
              Reserved.
            </div>
          </div>
        </footer>
        {/* Login panel======================= */}
        <div
          className={
            (login === true ? "slideInUp animated" : "d-none") +
            " right-side-popup-signup  scrollbar"
          }
        >
          <div
            className="px-5 pt-4"
            style={{
              background: `url(${back_img}) no-repeat center center`,
              backgroundSize: "cover",
              height: 100
            }}
          >
            <button
              type="button"
              className="close float-left login-signup-close text-white "
              aria-label="Close"
              onClick={cancelLoginSignup}

            >
              cancel
              <i className="fas fa-times f-110" />
            </button>
            <h3
              className="text-right text-white"
              style={{ textShadow: "5px 5px 5px #F00" }}
            >
              login /
              <button
                className="btn btn-sm btn-default btn-login"
                onClick={toggleSignupOpen}
              >
                Signup
              </button>
            </h3>
          </div>

          <div className="form view-form-search mt-4 px-4 py-3">
            <br />
            <div
              className={
                loginFormErr.success === true
                  ? "alert-success alert"
                  : loginFormErr.success === false ? "alert-danger alert" : ""
              }
            >
              {loginFormErr.message}
            </div>
            <form className="form-signin" onSubmit={this.handleLogin}>
              <div className="form-label-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Phone number"
                  minLength="10"
                  maxLength="10"
                  value={loginPhone}
                  onChange={e =>
                    e.target.value === "" || re.test(e.target.value)
                      ? this.setState({ loginPhone: e.target.value })
                      : ""}
                />
                <label htmlFor="phone_number" className="text-secondary">
                  Phone Number
                </label>
              </div>

              <div className="form-label-group text-right pt-3">
                <button
                  type="submit"
                  value="Continue"
                  className="btn btn-default btn-block"
                >
                  Continue
                </button>
              </div>
              <small className="text-secondary">
                By creating an account, I accept the
                <Link to="/" className="text-warning">
                  Terms &amp; Conditions
                </Link>
              </small>
            </form>
          </div>
        </div>
        {/* ================Signup Panel */}
        <div
          className={
            (signup === true ? "slideInUp animated" : "d-none") +
            " right-side-popup-signup  scrollbar"
          }
        >
          <div
            className="px-5 pt-4"
            style={{
              background: `url(${back_img}) no-repeat center center`,
              backgroundSize: "cover",
              height: 100
            }}
          >
            <button
              type="button"
              className="close float-left login-signup-close text-white "
              aria-label="Close"
              onClick={cancelLoginSignup}
            >
              cancel
              <i className="fas fa-times f-110" />
            </button>
            <h3
              className="text-right text-white"
              style={{ textShadow: "5px 5px 5px #F00" }}
            >
              Sign up /
              <button
                className="btn btn-sm btn-default btn-login"
                onClick={toggleLoginOpen}
              >
                Login
              </button>
            </h3>
          </div>

          <div className="form view-form-search mt-4 px-4 py-3">
            <br />
            <div
              className={
                signupFormError ? signupFormError.success === true
                  ? "alert-success alert"
                  : signupFormError.success === false
                    ? "alert-danger alert"
                    : "" :''
              }
            >
              { signupFormError ? signupFormError.message : ''}
            </div>
            <form className="form-signin" onSubmit={this.handleSignup}>
              <div className="form-label-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="name"
                  value={signupName}
                  onChange={e => this.setState({ signupName: e.target.value })}
                />
                <label htmlFor="name" className="text-secondary">
                  Name
                </label>
              </div>
              <div className="form-label-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Phone number"
                  minLength="10"
                  maxLength="10"
                  value={signupPhone}
                  onChange={e =>
                    e.target.value === "" || re.test(e.target.value)
                      ? this.setState({ signupPhone: e.target.value })
                      : ""}
                />
                <label htmlFor="phone_number" className="text-secondary">
                  Phone Number
                </label>
              </div>
              <div className="form-label-group">
                <input
                  type="email"
                  id="inputEmail"
                  className="form-control"
                  placeholder="Email address"
                  value={signupEmail}
                  onChange={e => this.setState({ signupEmail: e.target.value })}
                />
                <label htmlFor="email" className="text-secondary">
                  Email
                </label>
              </div>
              <div className="form-label-group">
                <input
                  type="password"
                  id="inputPassword"
                  className="form-control"
                  placeholder="Password"
                  minLength="6"
                  value={signupPassword}
                  onChange={e =>
                    this.setState({ signupPassword: e.target.value })}
                />
                <label htmlFor="password" className="text-secondary">
                  Password
                </label>
              </div>
              <div className="form-label-group text-right pt-3">
                <button
                  type="submit"
                  value="Continue"
                  className="btn btn-default btn-block"
                >
                  Continue
                </button>
              </div>
              <small className="text-secondary">
                By creating an account, I accept the
                <Link to="/" className="text-warning">
                  Terms &amp; Conditions
                </Link>
              </small>
            </form>
          </div>
        </div>

        <div className="scrolling">
          <a
            href="#pageTop"
            className="backToTop hidden-xs"
            id="backToTop"
            style={{ opacity: 1 }}
          >
            <i className="fa fa-arrow-up" aria-hidden="true" />
          </a>
        </div>
      </main>
    );
  }
}

export default Footer;
