import React, { Component } from "react";
import Header from "../../layouts/userHeader";
import men_img from "../../assets/img/men.png";
import AuthHelperMethods from "../../../components/AuthHelperMethods"
import withAuth from "../../../components/withAuth";
import request from "superagent";
import Profile from "./profile";
import BookingDetails from './BookingDetails';
import Booking from './Booking';
class UserDashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user:[],
      userEdit:false,
      activeBooking:true,
      activeBookingDetails:false,
      bookings:[],
      bookingDetails:[]
    };
  }
  Auth = new AuthHelperMethods();
  componentDidMount(){
    let confirm = this.props.confirm;
    // console.log("============User Dashboard");
    // console.log(confirm.username);
    let user_id = confirm.user_id;
    if(confirm){
      let url = `http://localhost:9000/api/account/user/${user_id}`;
    //  console.log(url);
      request.get(url)
      .then(res=>{
        let data = JSON.parse(res.text);
        // console.log("============User Dashboard");  
      // console.log(data);
        this.setState({user:data});
        let updateUser = this.state.user;
        updateUser.password = null;
        this.setState({user:updateUser});
      })
      .catch(err =>{
        console.log(err.stack);
      })
    
      request.get(`http://localhost:9000/api/serviceManagement/booking/getByUserId/${user_id}`)
           .then(res=>{
              let data = JSON.parse(res.text);
              // console.log(data);
              if(data.status){
                this.setState({bookings:data.result});
              }
           })
           .catch(err=>{console.log(err.stack)});
    }
  }

  onChangeName = name =>{
    let user = this.state.user;
    user.name = name;
    this.setState({user});
  }
  onChangeEmail = email =>{
    let user = this.state.user;
    user.email = email;
    this.setState({user});
  }
  onChangePhone = phone =>{
    let user = this.state.user;
    user.phone = phone;
    this.setState({user});
  }
  onChangePassword = password =>{
    let user = this.state.user;
    user.password = password;
    this.setState({user});
  }
  handleUserEdit = () =>{
    this.setState({userEdit:!this.state.userEdit});
  }
  handleUpdateUser = () =>{
    const {user} = this.state;
    if(user){
      let url = `http://localhost:9000/api/account/user/update/${user._id}`;
      console.log(url);
      request.post(url)
      .send(user)
      .then(res=>{
        let data = JSON.parse(res.text);
        if(data.status){
          this.setState({userEdit:false});
        }
        // alert(data.message);
        // console.log("============User Dashboard");  
        // console.log(res);
       // this.setState({user:data});
      })
      .catch(err =>{
        console.log(err.stack);
      })
    }
  }

  getUserBookingsByOrderNo = (order_no) =>{
    if(order_no){
      request.get(`http://localhost:9000/api/serviceManagement/bookingDetails/getByOrderNo/${order_no}`)
             .then(res=>{
               let data = JSON.parse(res.text);
               if(data.status){          
                 this.setState({bookingDetails:data.result,activeBookingDetails:true,activeBooking:false});
               }
             })
             .catch(err=>{console.log(err.stack)})
    }
  }
  handleBookingBackButton = () =>{
    this.setState({activeBooking:true,activeBookingDetails:false});
  }
  render() {
    const {user,userEdit,bookings,bookingDetails,activeBooking,activeBookingDetails} = this.state;
    // console.log(user);
    return (
      <React.Fragment>
        <Header />
        <div
          className="container border bg-white rounded shadow user-dashboard"
          style={{ marginTop: -50 }}
        >
          <div className="row ">
            <div className="col-12 mx-auto  text-center">
              <img
                src={men_img}
                className="img rounded-circle  shadow"
                height="150"
                width="150"
                alt="user img"
                style={{ marginTop: -70 }}
              />
              <h3 className="mt-3"> {user.name}</h3>
              <hr style={{ border: "1px  dotted" }} />
            </div>
          </div>
          <div className="row text-center bg-light mt-3 p-3">
            <div className="col-sm">
              <i className="fas fa-map-marker-alt pr-2 f-150 py-1" />Address........
            </div>
            <div className="col-sm">
              <i className="fas fa-mobile-alt pr-2 f-150 py-1" />{user.phone}
            </div>
            <div className="col-sm">
              <i className="far fa-envelope pr-2 f-150 py-1" />
              {user.email}
            </div>
          </div>

          <div
            className="container bg-light pt-4 wow zoomIn"
            style={{ marginTop: 90 }}
          >
            <div className="row">
              {/* <!--========Our Dashboard Tabs --> */}
              <div className="col-sm-3">
                <div
                  className="nav flex-column nav-pills user-nav-tabs"
                  id="#v-pills-tab"
                  role="tablist"
                  aria-orientation="vertical "
                >
                  <a
                    className="nav-link active rounded-pill"
                    id="v-pills-profile-tab"
                    data-toggle="pill"
                    href="#v-pills-profile"
                    role="tab"
                    aria-controls="v-pills-profile"
                    aria-selected="true"
                   >
                    <i className="fas fa-receipt pr-3" />
                    Profile
                  </a>
                  <a
                    className="nav-link  rounded-pill"
                    id="v-pills-home-tab"
                    data-toggle="pill"
                    href="#v-pills-home"
                    role="tab"
                    aria-controls="v-pills-home"
                    aria-selected="true"
                   >
                    <i className="fas fa-receipt pr-3" />
                    Booking
                  </a>
                  <a
                    className="nav-link rounded-pill"
                    id="v-pills-fav-tab"
                    data-toggle="pill"
                    href="#v-pills-fav"
                    role="tab"
                    aria-controls="v-pills-fav"
                    aria-selected="false"
                  >
                    <i className="far fa-thumbs-up pr-3" />Favourites
                  </a>
                  <a
                    className="nav-link rounded-pill"
                    id="v-pills-payment-tab"
                    data-toggle="pill"
                    href="#v-pills-payment"
                    role="tab"
                    aria-controls="v-pills-payment"
                    aria-selected="false"
                  >
                    <i className="fas fa-wallet pr-3" />Payments
                  </a>
                 
                </div>
              </div>
              {/* <!--========End Our Dashboard Tabs --> */}
              {/* <!--========Our Tabs Detalis Section --> */}
              <div
                className="col-sm-9 bg-light"
                style={{ height: 430, overflow: "auto" }}
              >

                <div className="tab-content" id="v-pills-tabContent">
                    {/* <!--========Address Tab --> */}
                    <div
                    className="tab-pane fade show active"
                    id="v-pills-profile"
                    role="tabpanel"
                    aria-labelledby="v-pills-profile-tab"
                    >
                    <Profile user={user}
                             onChangeName={this.onChangeName}
                             onChangeEmail={this.onChangeEmail}
                             onChangePhone={this.onChangePhone}
                             onChangePassword={this.onChangePassword}
                             handleUpdateUser={this.handleUpdateUser}
                             handleUserEdit = {this.handleUserEdit}
                             edit = {userEdit}/>
                  </div>
                  {/* <!--========End Address Tab --> */}
                  {/* <!--========Booking Tab --> */}
                  <div
                    className={activeBookingDetails?'tab-pane fade':'tab-pane fade'}
                    id="v-pills-home"
                    role="tabpanel"
                    aria-labelledby="v-pills-home-tab"
                    >
                  {activeBooking ?  
                        <Booking bookings={bookings} 
                           activeBooking={activeBooking} 
                           getUserBookingsByOrderNo = {this.getUserBookingsByOrderNo}/> 
                           : ''}
                  {activeBookingDetails? 
                        <React.Fragment>
                        <button className="btn btn-outline-danger" onClick={this.handleBookingBackButton}>back</button>
                        <BookingDetails bookingDetails = {bookingDetails} 
                           activeBookingDetails = {activeBookingDetails} />
                           </React.Fragment>
                   :''}
                  </div>
                 
                 
                                
                  {/* <!--========End Booking Tab --> */}
                  {/* <!--========Favouzrits Tab --> */}
                  <div
                    className="tab-pane fade border border-left-0 bg-white"
                    id="v-pills-fav"
                    style={{ height: 410, paddingTop: 100 }}
                    role="tabpanel"
                    aria-labelledby="v-pills-fav-tab"
                   >
                    <div className="text-center my-auto ">
                      <i
                        className="far fa-thumbs-up text-secondary "
                        style={{ fontSize: "800%" }}
                      />
                      
                        <h5>Where is the love?</h5>
                        Once you favourite a restaurant, it will appear here.
                      
                    </div>
                  </div>
                  {/* <!--========Ends Favourits Tab --> */}
                  {/* <!--========Payments Tab --> */}
                  <div
                    className="tab-pane fade bg-white p-3"
                    id="v-pills-payment"
                    role="tabpanel"
                    aria-labelledby="v-pills-payment-tab"
                  >
                    <h1>Pyments</h1>
                    <p>Linked Wallets</p>
                    <div className="row px-4">
                      <div className="col-3 col-md-1 pr-0 py-3 border border-right-0  ">
                        <img
                          className="pt-2 "
                          src="https://res.cloudinary.com/swiggy/image/upload/fl_lossy,f_auto,q_auto,e_trim/phonepe_icon_aca9jf "
                          height="28 "
                        />
                      </div>
                      <div className="col-9 col-md-4 pl-0 py-3 border border-left-0 ">
                        <div className="float-left ">
                          <div>PhonePe</div>
                          <div>
                            <small className="text ">Balance: 0</small>
                          </div>
                        </div>
                        <a href="# " className="float-right pt-2 ">
                          DLINK
                        </a>
                      </div>
                    </div>
                  </div>
                  {/* <!--========End Payments Tab --> */}
                
                </div>
              </div>
              {/* <!--========End Our Tabs Detalis Section --> */}
            </div>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default withAuth(UserDashboard);
