import React, { Component } from 'react'
import request from "superagent";

class BookingDetails extends Component{
    constructor(props){
        super(props);
        this.state={
            userBookingDetailsViaState:[]
        }
    }

   componentDidMount(){
    if(this.props.bookingDetails){
        let result = [...this.props.bookingDetails];
            result  = result.map(r=>{
            request.get(`http://localhost:9000/getVendorData/api/getVendorByVendorId/${r.vendor_id}`)
            .then(res=>{
                let data = res.body;
                if(data.status){
                    r['vendor'] = data.result;
                }
            })
            .catch(err=>{console.log(err.stack)});
                return r;
            });
            console.log(result);
       
        this.setState({userBookingDetailsViaState:result});
    }

   // console.log(this.state.userBookingDetailsViaState);
  
   }
    render(){
        this.state.userBookingDetailsViaState.map(e=>{
            console.log("vendor");
            console.log(e.booking_amount);
            console.log(e.vendor);
        });
        const {activeBookingDetails,bookingDetails} = this.props;
        // console.log(bookingDetails);
        // console.log("===============================");
    
        // console.log(bookingDetails);
        return(
            <React.Fragment>
                  {/* <!--========Booking Tab --> */}
                  
                    {/* <!--========Booked Service --> */}
                {bookingDetails.map(bd => (
                    <div className="card p-2 mb-5 border border-left-0  "> 
                        <div className="card-body">
                            <blockquote className=" mb-0 ">
                            <div className="row ">
                                <div className="co-md-2 d-none d-md-block ">
                                <img
                                    src="http://wowtis.in/final_project/user/assets/img/1.png"
                                    alt=""
                                    height="100 "
                                />
                                </div>
                                <div className="col-7 col-md-5 col-sm-9 text-left text ">
                                <h5 className="mb-0 ">
                                    <a href="">{bd.vendor} test</a>
                                </h5>
                                <small>
                                    Indira nagar ,Gorakhpur<br />
                                    <span className="d-none d-md-block ">
                                    ORDER #42390215326 | Fri, May 31, 8:33 PM
                                    <br />
                                    <a href="# " className="view_details ">
                                        View Details
                                    </a>
                                    <a href="# " className="pl-2 view_details ">
                                        Invoice
                                    </a>
                                    </span>
                                </small>
                                </div>
                                <div className="col col-sm text-right ">
                                <div className="d-none d-md-block ">
                                    <span>Serviced on Fri, May 31, 9:00 PM</span>
                                    <i
                                    className="fas fa-check-circle text-success "
                                    style={{ fontSize: "150%" }}
                                    />
                                    <br />
                                    <br />
                                    <span className="bg-default py-2 px-3 rounded-pill ">
                                    Total Paid :  {bd.booking_amount}
                                    </span>
                                </div>
                                <div className="d-block d-md-none ">
                                    <span> 250</span>
                                    <br />
                                    <a href="# " className="pl-2 view_details ">
                                    Invoice
                                    </a>
                                </div>
                                </div>
                            </div>
                            <hr style={{ border: "1px dotted" }} />
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th>Service name</th>
                                        <th>amount</th>
                                        <th>discount</th>
                                        <th>stylist name</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {bd.service.map(s=>(
                                        <tr key={s._id}>
                                            <td></td>
                                            <td>{s.service_amount}</td>
                                            <td>{s.service_discount==='' || s.service_discount===null ? '0 %' : s.service_discount+' %'}</td>
                                            <td>{s.stylist_name}</td>
                                        </tr>
                                    ))}
                                   
                                </tbody>
                            </table>
                            <button className="btn btn-default btn-sm px-3 py-2 btn-login ">
                                Re - Book
                            </button>
                            <button className="btn border  btn-sm ml-3 px-3 py-2 btn-signup ">
                                Help
                            </button>
                            {/* <!-- <footer className="blockquote-footer ">Someone famous in <cite title="Source Title ">Source Title</cite></footer>--> */}
                            </blockquote>
                        </div>
                    </div>
                        
                    
                ))}
                
                    {/* <!--========End Booked Service --> */}
                  
                  {/* <!--========End Booking Tab --> */}
            </React.Fragment>
        );
    }
} 

export default BookingDetails;