import React, { Component } from "react";

import logo from "../assets/logo-trans.png";
import "../assets/css/styles.css";
import { Link } from "react-router-dom";
import AuthHelperMethods from "../../components/AuthHelperMethods";
const Auth = new AuthHelperMethods();
class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users:[]
    };
  }
  componentDidMount(){
    
  }
  render() {
    return (
      <React.Fragment>
        <header style={{ height: 230 }}>
          <nav
            className="shadow-sm navbar fixed-top navbar-white bg-white"
            data-wow-duration="1s"
            data-wow-delay="1s"
          >
            <div className="container px-0">
              <Link
                className="navbar-brand rounded-circle bg-white"
                id="logo"
                style={{ position: "fixed", top: 0 }}
                to="/"
              >
                <img src={logo} alt="" height="100" width="" />
              </Link>
              <a
                className="navbar-brand  bg-white"
                href="index.php"
                id="logo"
              />

              <div className="my-2 my-lg-0">
                <div className="btn-group categroy-icon">
                  <button
                    type="button"
                    className="btn btn-link dropdown-toggle"
                    data-toggle="dropdown"
                    data-display="static"
                    aria-haspopup="true"
                    aria-expanded="false"
                    id="category-dropdown"
                  >
                    <svg
                      width="30px"
                      height="30px"
                      viewBox="0 0 24 24"
                      version="1.1"
                      className="ue-b8"
                    >
                      <g>
                        <g transform="translate(4.000000, 4.000000)">
                          <rect x="0" y="13" width="3" height="3" />
                          <rect x="0" y="6.5" width="3" height="3" />
                          <rect x="13" y="13" width="3" height="3" />
                          <rect x="13" y="6.5" width="3" height="3" />
                          <rect x="6.5" y="13" width="3" height="3" />
                          <rect x="6.5" y="6.5" width="3" height="3" />
                          <rect x="0" y="0" width="3" height="3" />
                          <rect x="13" y="0" width="3" height="3" />
                          <rect x="6.5" y="0" width="3" height="3" />
                        </g>
                      </g>
                    </svg>
                    <span className="pl-2 ">Categories</span>
                  </button>
                  <div
                    className="dropdown-menu dropdown-menu-right dropdown-menu-lg-left wow slideInRight"
                    data-wow-offset="0"
                  >
                    <Link className="dropdown-item" to="/list">
                      Salon
                    </Link>
                    <Link
                      className="dropdown-item"
                      to="/list"
                    >
                      Spa
                    </Link>
                   
                  </div>
                </div>
              </div>
            </div>
          </nav>
        </header>
      </React.Fragment>
    );
  }
}

export default Header;
